.page-load {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #141414;
  z-index: 99;
  opacity: 1;
  visibility: visible;
  pointer-events: painted;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f9f9f9;
}

.page-load.act {
  -webkit-animation: pageload 0.3s linear forwards;
          animation: pageload 0.3s linear forwards;
}

@-webkit-keyframes pageload {
  0% {
    opacity: 1;
    visibility: visible;
    pointer-events: painted;
  }
  100% {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }
}

@keyframes pageload {
  0% {
    opacity: 1;
    visibility: visible;
    pointer-events: painted;
  }
  100% {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }
}

