.preloader {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f9f9f9;
}
.preloader::before {
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  transition: 0.8s cubic-bezier(0.7, 0, 0.3, 1) 0.5s;
}
.preloader::before {
  background: #bd9165;
  left: 0;
}
.preloader.act::before {
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}
.preloader-a {
  position: absolute;
  width: 1.5vw;
  min-width: 25px;
  height: 1.5vw;
  min-height: 25px;
  z-index: 2;
}
.preloader-a::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #f9f9f9;
  -webkit-animation: 1s preloaderA cubic-bezier(0, 0, 0.2, 1) infinite;
          animation: 1s preloaderA cubic-bezier(0, 0, 0.2, 1) infinite;
}
@-webkit-keyframes preloaderA {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes preloaderA {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.preloader.act .preloader-a {
  -webkit-transform: translateY(55vh);
          transform: translateY(55vh);
  opacity: 0;
  transition: opacity 0.4s ease 0.4s,
    -webkit-transform 0.9s cubic-bezier(0.7, 0, 0.3, 1);
  transition: transform 0.9s cubic-bezier(0.7, 0, 0.3, 1),
    opacity 0.4s ease 0.4s;
  transition: transform 0.9s cubic-bezier(0.7, 0, 0.3, 1),
    opacity 0.4s ease 0.4s,
    -webkit-transform 0.9s cubic-bezier(0.7, 0, 0.3, 1);
}
.preloader-i {
  position: absolute;
  bottom: 3vh;
  font-size: 2vw;
  line-height: 1.5vw;
  text-transform: uppercase;
  font-family: "Bebas Neue", cursive;
  z-index: 2;
}
.preloader.act .preloader-i {
  -webkit-transform: translateY(30vh);
          transform: translateY(30vh);
  opacity: 0;
  transition: opacity 0.4s ease 0.4s,
    -webkit-transform 0.9s cubic-bezier(0.7, 0, 0.3, 1);
  transition: transform 0.9s cubic-bezier(0.7, 0, 0.3, 1),
    opacity 0.4s ease 0.4s;
  transition: transform 0.9s cubic-bezier(0.7, 0, 0.3, 1),
    opacity 0.4s ease 0.4s,
    -webkit-transform 0.9s cubic-bezier(0.7, 0, 0.3, 1);
}

