  .preloader::before,
  .preloader::after {
    width: 100%;
    height: 100%;
    -webkit-transform: translateX(0);
            transform: translateX(0);
    transition: 0.7s cubic-bezier(0.7, 0, 0.3, 1) 0.6s;
  }
  .preloader.act::before,
  .preloader.act::after {
    -webkit-transform: translateX(-100vw);
            transform: translateX(-100vw);
  }
  .preloader.act .preloader-a,
