.soc-icon-box {
  position: relative;
  display: flex;
  flex-direction: row;
}
.soc-icons {
  width: 2.2vw;
  height: 2.2vw;
  min-width: 35px;
  min-height: 35px;
  border-radius: 50%;
  margin-right: 1vw;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: relative;
  transition: 0.4s ease;
}
.soc-icons svg {
  position: absolute;
  width: 50%;
  height: 50%;
  fill:#222
}

.soc-icons::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #f9f9f9;
  border-radius: 50%;
  transition: 0.4s ease;
  z-index: -1;
}
.soc-icons:hover::before {
  background: #bd9165;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

