.cursor-dot,
.cursor-dot-outline {
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  opacity: 1;
  transition: opacity 0.3s ease-in-out, background-color 0.3s ease-in-out, border 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out,
    background-color 0.3s ease-in-out, border 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out,
    background-color 0.3s ease-in-out, border 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
  z-index: 99;
}

.cursor-dot {
  width: 0px;
  height: 0px;
  border-top: 11px solid transparent;
  border-bottom: 7px solid transparent;
  border-left: 13px solid #f9f9f9;
  display: none;
}

#cursor-dot::before {
  content: "";
  width: 0px;
  height: 0px;
  border-top: 12px solid transparent;
  border-bottom: 6px solid transparent;
  border-left: 14px solid #f9f9f9;
  position: absolute;
  left: 0;
  top: 0;
  -webkit-transform: translate(-90%, -80%);
          transform: translate(-90%, -80%);
}

.cursor-dot-outline {
  position: absolute;
  width: 5.5vw;
  height: 5.5vw;
  border-radius: 50%;
  background-color: #f9f9f9;
  /* mix-blend-mode:difference; */
  -webkit-transform: translate(-50%, -50%) scale(0.1);
          transform: translate(-50%, -50%) scale(0.1);
  justify-content: center;
  align-items: center;
  color: #f9f9f9;
  opacity: 0;
}

.cursor-eye,
.cursor-back,
.cursor-more,
.cursor-next {
  position: absolute;
  width: 5.5vw;
  height: 5.5vw;
  top: 0;
  left: 0;
  opacity: 1;
}
.cursor-eye {
  background: #f9f9f9 url(/saa/static/media/cursor-eye.e9e202c2.svg) no-repeat 50% / 45%;
}
.cursor-back {
  background: #f9f9f9 url(/saa/static/media/cursor-back.0d15aa2b.svg) no-repeat 50% / 43%;
}
.cursor-more {
  background: #f9f9f9 url(/saa/static/media/cursor-more.6684050e.svg) no-repeat 50% / 43%;
}
.cursor-prev {
  background: #f9f9f9 url(/saa/static/media/cursor-prev.c58652f1.svg) no-repeat 50% / 43%;
}
.cursor-next {
  background: #f9f9f9 url(/saa/static/media/cursor-next.88fc15d7.svg) no-repeat 50% / 43%;
}

