@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navigation,
.page-nav-a,
button,
.content-pagination a {
  -webkit-touch-callout: none;
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  user-select: none;
}

::-webkit-scrollbar-track {
  background: none;
}

::-webkit-scrollbar-thumb {
  background: #bd9165;
  border-radius: 5px;
  width: 0.2vw;
}

::selection {
  background: #bd9165;
  color: #141414;
}

::-webkit-scrollbar {
  width: 0;
  height: 0;
}

body {
  font-family: sans-serif;
  background-color: #141414;
  font-size: 0.8vw;
  line-height: 0.8vw;
}

body.act{
  position: fixed;
  width: 100%;
  max-height: 100%;
  
}


body.white,
body.white .wel-h2-d::before,
body.white .nav-menu {
  background-color: #c9c6b5;
}
body.black {
  background-color: #141414;
}

body.white h2,
body.white h3,
body.white h4,
body.white p,
body.white .wel-h2 h2.bp-h2-w,
body.white .content-pagination a h3 {
  color: #222;
}
body.white .nav-menu-l a h2,
body.white .wel-h2 h4,
body.white .pin-wrap h3,
body.white .nav-menu-c-a h4,
body.white .blog-navigation h4:hover,
body.white .blog-navigation h4:focus,
body.white .panel.red p,
body.white .preloader-i {
  color: #f9f9f9;
}
body.white .nav-menu-l a.active h2 {
  color: #222;
}
body.white svg {
  fill: #222;
}
body.white .m-page-r,
body.white .preloader::before,
body.white .img-cont,
body.white hr,
body.white .footer::before,
body.white .panel.red,
body.white .card-image {
  background-color: #222;
}
body.white .card-image-container .title-text {
  background-color: #f9f9f9;
}
body.white .soc-icons::before {
  background: #141414;
}

body.white .soc-icons svg {
  fill:#f9f9f9
}

