html.has-scroll-smooth {
  overflow: hidden;
}
html.has-scroll-dragging {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.has-scroll-smooth body {
  overflow: hidden;
}
.has-scroll-smooth [data-scroll-container] {
  min-height: 100vh;
}
.scrollbar {
  position: fixed;
  right: 0.5vw;
  top: 5%;
  width: 11px;
  height: 90%;
  transform-origin: center right;
  transition: transform 0.3s, opacity 0.3s;
  opacity: 0;
  z-index: 90;
}
.scrollbar:hover,
.has-scroll-scrolling .scrollbar,
.has-scroll-dragging .scrollbar {
  opacity: 1;
}
[data-scroll-direction="horizontal"] .scrollbar {
  width: 100%;
  height: 10px;
  top: auto;
  bottom: 0;
  transform: scaleY(1);
}
.scrollbar_thumb {
  position: absolute;
  top: 0;
  right: 0;
  background-color: #f9f9f9;
  opacity: 0.9;
  width: 0.4vw;
  min-width: 7px;
  min-height: 100px;
  max-height: 100px;
  margin: 2px;
  cursor: default;
  border-radius: 50px;
}
.has-scroll-dragging .scrollbar_thumb {
  cursor: default;
}
[data-scroll-direction="horizontal"] .scrollbar_thumb {
  right: auto;
  bottom: 0;
}

hr {
  position: relative;
  width: 50%;
  top: 0;
  background: #f9f9f9;
  height: 0.3vw;
  border: none;
}

h2 {
  position: relative;
  /* font-size: calc(40px + 5.5vw);
  line-height: calc(30px + 5vw); */
  font-size: 8vw;
  line-height: 6.5vw;
  font-family: "Bebas Neue", cursive;
  text-align: right;
}
h3 {
  position: relative;
  font-size: 5vw;
  line-height: 4.1vw;
  font-family: "Bebas Neue", cursive;
  text-align: right;
}

h4 {
  font-size: 2vw;
  line-height: 1.9vw;
  text-transform: uppercase;
  font-family: "Bebas Neue", cursive;
  padding: 15px 0;
}

h5 {
  font-size: 1.3vw;
  line-height: 1.3vw;
  text-transform: uppercase;
  font-family: "Bebas Neue", cursive;
}

p {
  padding: 0.7vw 0;
  font-size: 0.9vw;
  line-height: 1.2vw;
}

button {
  position: relative;
  cursor: pointer;
  padding: 0 calc(20px + 1.2vw);
  margin: calc(20px + 0.5vw) 0;
  width: auto;
  height: 3.7vw;
  min-height: 50px;
  border: none;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2vw;
  line-height: 1vw;
  text-transform: uppercase;
  font-family: "Bebas Neue", cursive;
}

button::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  background: #f9f9f9;
  z-index: -1;
  transition: 0.4s ease-in;
  border-radius: 100px;
  transform: scale(1);
}

button:hover::before {
  background: #bd9165;
  transform: scale(1.1);
}

.sth-scrl {
  position: relative;
  width: 100%;
  height: auto;
  min-height: 100vh;
  top: 0;
  left: 0;
}

.pin-spacer {
  width: 101%;
}

.page-load {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #141414;
  z-index: 99;
  opacity: 1;
  visibility: visible;
  pointer-events: painted;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f9f9f9;
}

.page-load.act {
  animation: pageload 0.3s linear forwards;
}

@keyframes pageload {
  0% {
    opacity: 1;
    visibility: visible;
    pointer-events: painted;
  }
  100% {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }
}

.m-page-cont {
  position: relative;
  width: 100%;
  top: 0;
  left: 0;
}

.m-page-box {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 99;
}

.m-page-r {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  background: #bd9165;
  pointer-events: painted;
}
.m-page-r {
  right: 0;
}

#main-box {
  width: 100%;
  height: auto;
  top: 0;
  left: 0;
  position: relative;
}

.preloader {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f9f9f9;
}
.preloader::before {
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  transition: 0.8s cubic-bezier(0.7, 0, 0.3, 1) 0.5s;
}
.preloader::before {
  background: #bd9165;
  left: 0;
}
.preloader.act::before {
  transform: translateY(100%);
}
.preloader-a {
  position: absolute;
  width: 1.5vw;
  min-width: 25px;
  height: 1.5vw;
  min-height: 25px;
  z-index: 2;
}
.preloader-a::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #f9f9f9;
  animation: 1s preloaderA cubic-bezier(0, 0, 0.2, 1) infinite;
}
@keyframes preloaderA {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.preloader.act .preloader-a {
  transform: translateY(55vh);
  opacity: 0;
  transition: transform 0.9s cubic-bezier(0.7, 0, 0.3, 1),
    opacity 0.4s ease 0.4s;
}
.preloader-i {
  position: absolute;
  bottom: 3vh;
  font-size: 2vw;
  line-height: 1.5vw;
  text-transform: uppercase;
  font-family: "Bebas Neue", cursive;
  z-index: 2;
}
.preloader.act .preloader-i {
  transform: translateY(30vh);
  opacity: 0;
  transition: transform 0.9s cubic-bezier(0.7, 0, 0.3, 1),
    opacity 0.4s ease 0.4s;
}

.img-cont {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #bd9165;
  z-index: 1;
  transform: scale(1.3);
}

.container {
  width: 100%;
  padding: 5% 20%;
  position: relative;
  top: 0;
  left: 0;
  z-index: 2;
}

.rotate-device {
  display: none;
}

@media screen and (max-width: 1024px) {
  button {
    font-size: 16px;
    line-height: 16px;
  }
  button:hover::before {
    background: #f9f9f9;
    transform: scale(1);
  }

  body {
    font-size: 14px;
    line-height: 14px;
  }

  h2 {
    font-size: calc(40px + 5.5vw);
    line-height: calc(35px + 4.5vw);
  }

  h3 {
    font-size: calc(30px + 3vw);
    line-height: calc(28px + 2.5vw);
  }

  h4 {
    padding: 15px 0;
    font-size: 2vw;
    line-height: 1.5vw;
  }

  p {
    padding: 10px 0;
    font-size: 16px;
    line-height: 20px;
  }

  .container {
    width: 100%;
    padding: 10% 10%;
  }
}

@media screen and (max-width: 768px) {
  h4,
  .preloader-i {
    font-size: calc(20px + 1.2vw);
    line-height: calc(18px + 1vw);
  }
  .preloader::before,
  .preloader::after {
    width: 100%;
    height: 100%;
    transform: translateX(0);
    transition: 0.7s cubic-bezier(0.7, 0, 0.3, 1) 0.6s;
  }
  .preloader.act::before,
  .preloader.act::after {
    transform: translateX(-100vw);
  }
  .preloader.act .preloader-a,
  .preloader.act .preloader-i {
    transform: translateX(-60vw);
  }
}

@media (min-width: 540px) and (max-height: 450px) {
  .rotate-device {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: #141414;
    color: #f9f9f9;
    z-index: 99999;
  }
  .rotate-device div:nth-child(1) {
    position: relative;
    width: 160px;
    height: 100px;
    background: url("./components/images/rotate-portrait.png") no-repeat 50% /
      100%;
  }
}
