.theme-app {
  width: 1.8vw;
  height: 1.8vw;
  min-width: 30px;
  min-height: 30px;
  background-repeat: no-repeat;
  background-position: 50%;
  position: absolute;
  top: 3.5vw;
  left: 50%;
  transform: translateX(-50%);
  pointer-events: painted;
  cursor: pointer;
  background-size: 90%;
  z-index: 5;
  transition: 0.3s ease;
}

