.navigation {
  position: fixed;
  width: 100%;
  height: auto;
  /* background: rgba(255, 255, 255, 0.2); */
  z-index: 98;
  display: flex;
  align-items: center;
  padding: 3% 5%;
  justify-content: space-between;
  pointer-events: none;
}

.navigation .active,
.navigation .active .logo {
  pointer-events: none;
}

.logo {
  width: 2.5vw;
  height: 2.5vw;
  min-width: 40px;
  min-height: 40px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 3;
  pointer-events: painted;
}

.logo svg {
  position: absolute;
  width: 90%;
  height: 90%;
  fill: #f9f9f9;
}

.logo .log-3 {
  transition: 0.4s ease-out;
}
.logo .log-1,
.logo .log-2,
.logo .log-4,
.logo .log-5 {
  transform-origin: center;
  transition: 0.4s ease-out;
}
.logo:hover .log-1 {
  transform: translate(-20%, 20%);
}
.logo:hover .log-2 {
  transform: translate(-10%, -5%);
}
.logo:hover .log-4 {
  transform: translate(15%, 10%);
}
.logo:hover .log-5 {
  transform: translate(10%, -10%);
}

.nm-btn {
  position: relative;
  width: 2.7vw;
  height: 2.7vw;
  min-width: 45px;
  min-height: 45px;
  z-index: 3;
  border-radius: 50%;
  cursor: pointer;
  pointer-events: painted;
}
.nm-btn svg {
  fill: #f9f9f9;
}
.nm-btn polygon:nth-child(1),
.nm-btn polygon:nth-child(2),
.nm-btn polygon:nth-child(3),
.nm-btn polygon:nth-child(4),
.nm-btn polygon:nth-child(5) {
  transform: translate(0, 0) rotate(0deg);
  transform-origin: center;
  transition: 0.5s cubic-bezier(0.7, 0, 0.3, 1);
  opacity: 1;
}
.nm-btn.no-act polygon:nth-child(1),
.nm-btn.no-act polygon:nth-child(2),
.nm-btn.no-act polygon:nth-child(3),
.nm-btn.no-act polygon:nth-child(4),
.nm-btn.no-act polygon:nth-child(5) {
  transition: none;
}
.nm-btn polygon:nth-child(3),
.nm-btn polygon:nth-child(4) {
  opacity: 0;
}
.nm-btn.act polygon:nth-child(1) {
  transform: translateY(-50%);
  opacity: 0;
}
.nm-btn.act polygon:nth-child(2) {
  /* transform: translateX(50%); */
  opacity: 0;
  transition: opacity 0.4s linear 0.2s;
}
.nm-btn.act polygon:nth-child(5) {
  transform: translateY(50%);
  opacity: 0;
}
.nm-btn.act polygon:nth-child(3) {
  transform: rotate(45deg);
  opacity: 1;
  transition: transform 0.6s cubic-bezier(0.7, 0, 0.3, 1) 0.2s,
    opacity 0.2s linear 0s;
}
.nm-btn.act polygon:nth-child(4) {
  transform: rotate(-45deg);
  opacity: 1;
  transition: transform 0.6s cubic-bezier(0.7, 0, 0.3, 1) 0.2s,
    opacity 0.2s linear 0s;
}

.nav-menu {
  display: flex;
  flex-direction: row;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #141414;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(-100%);
  transition: 0.4s cubic-bezier(0.7, 0, 0.3, 1);
}
.nav-menu.act {
  visibility: visible;
  pointer-events: painted;
  transform: translateY(0%);
  transition: 0.8s cubic-bezier(0.7, 0, 0.3, 1);
}

.nav-menu.no-act {
  transition: none;
}

.nav-menu-l,
.nav-menu-c {
  width: 100%;
  height: 100%;
  position: relative;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-menu-l {
  font-family: "Bebas Neue", cursive;
}

.nav-menu-c {
  overflow: hidden;
  color: #f9f9f9;
}

.nav-menu-c::before {
  content: "";
  width: 5px;
  height: 30%;
  position: absolute;
  background: #f9f9f9;
  left: 0;
  border-radius: 50px;
}

.nav-menu-c-b {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 2;
  pointer-events: none;
  background-color: #bd9165;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
}

.nav-menu-c-a {
  position: relative;
  top: 0;
  left: 0;
  transform: translateY(-20vh);
  opacity: 0;
  transition: 0.2s cubic-bezier(0.7, 0, 0.3, 1);
}

.nav-menu-c-a h4 {
  color: #bd9165;
}

.nav-menu-c-a div p {
  user-select: text;
}

.act .nav-menu-c .nav-menu-c-a {
  transform: translateY(0);
  opacity: 1;
  transition: 1s cubic-bezier(0.7, 0, 0.3, 1) 0.2s;
}

.nav-menu-l ul li:nth-child(1),
.nav-menu-l ul li:nth-child(2),
.nav-menu-l ul li:nth-child(3),
.nav-menu-l ul li:nth-child(4) {
  transform: translateY(-20vh);
  opacity: 0;
  transition: 0.4s cubic-bezier(0, 0, 0.2, 1);
}
.act .nav-menu-l ul li:nth-child(1),
.act .nav-menu-l ul li:nth-child(2),
.act .nav-menu-l ul li:nth-child(3),
.act .nav-menu-l ul li:nth-child(4) {
  opacity: 1;
  transform: translateY(0);
  transition: 0.4s cubic-bezier(0, 0, 0.2, 1);
}
.act .nav-menu-l ul li:nth-child(1) {
  transition: 0.7s cubic-bezier(0, 0, 0.2, 1) 0.6s;
}
.act .nav-menu-l ul li:nth-child(2) {
  transition: 0.7s cubic-bezier(0, 0, 0.2, 1) 0.5s;
}
.act .nav-menu-l ul li:nth-child(3) {
  transition: 0.7s cubic-bezier(0, 0, 0.2, 1) 0.4s;
}
.act .nav-menu-l ul li:nth-child(4) {
  transition: 0.7s cubic-bezier(0, 0, 0.2, 1) 0.3s;
}

.nav-menu-l ul li {
  margin: 0 1vh;
  list-style-type: none;
  text-align: right;
}

.nav-menu-l ul li a {
  display: block;
  color: #f9f9f9;
  text-decoration: none;
  font-size: 5.4vw;
  line-height: 6.6vw;
  position: relative;
  transition: 0.5s ease;
  cursor: pointer;
  z-index: 2;
}

.nav-menu-l ul li a.active {
  color: #bd9165;
}

.nav-menu-l ul li a span {
}

.nav-menu-l ul li a span:nth-child(1),
.nav-menu-l ul li a span:nth-child(2),
.nav-menu-l ul li a span:nth-child(3),
.nav-menu-l ul li a span:nth-child(4),
.nav-menu-l ul li a span:nth-child(5),
.nav-menu-l ul li a span:nth-child(6),
.nav-menu-l ul li a span:nth-child(7) {
  position: relative;
  left: 0%;
}
.nav-menu-l ul li a:hover span:nth-child(1) {
  animation: 0.4s navmenul ease;
}
.nav-menu-l ul li a:hover span:nth-child(2) {
  animation: 0.4s navmenul ease 0.1s;
}
.nav-menu-l ul li a:hover span:nth-child(3) {
  animation: 0.4s navmenul ease 0.2s;
}
.nav-menu-l ul li a:hover span:nth-child(4) {
  animation: 0.4s navmenul ease 0.3s;
}
.nav-menu-l ul li a:hover span:nth-child(5) {
  animation: 0.4s navmenul ease 0.4s;
}
.nav-menu-l ul li a:hover span:nth-child(6) {
  animation: 0.4s navmenul ease 0.5s;
}
.nav-menu-l ul li a:hover span:nth-child(7) {
  animation: 0.4s navmenul ease 0.6s;
}
@keyframes navmenul {
  0% {
    left: 0%;
    color: #f9f9f9;
  }
  25% {
    left: 8%;
    color: #bd9165;
  }
  100% {
    left: 0%;
    color: #f9f9f9;
  }
}

.nav-menu-l ul li img{
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  z-index: 1;
 }

.nav-menu-l ul li.act img{
  display: block;
}

.soc-icon-box {
  position: relative;
  display: flex;
  flex-direction: row;
}
.soc-icons {
  width: 2.2vw;
  height: 2.2vw;
  min-width: 35px;
  min-height: 35px;
  border-radius: 50%;
  margin-right: 1vw;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: relative;
  transition: 0.4s ease;
}
.soc-icons svg {
  position: absolute;
  width: 50%;
  height: 50%;
  fill:#222
}

.soc-icons::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #f9f9f9;
  border-radius: 50%;
  transition: 0.4s ease;
  z-index: -1;
}
.soc-icons:hover::before {
  background: #bd9165;
  transform: scale(1.2);
}

@media screen and (max-width: 1024px) {
  .theme-app {
    top: 50px;
  }
  body.act {
    overflow: hidden;
  }
  .navigation {
    padding: 40px 50px;
  }
  .logo:hover .log-1,
  .logo:hover .log-2,
  .logo:hover .log-4,
  .logo:hover .log-5 {
    transition: none;
    transform: translate(0, 0);
  }
  .nav-menu-l ul li a:hover span:nth-child(1),
  .nav-menu-l ul li a:hover span:nth-child(2),
  .nav-menu-l ul li a:hover span:nth-child(3),
  .nav-menu-l ul li a:hover span:nth-child(4),
  .nav-menu-l ul li a:hover span:nth-child(5),
  .nav-menu-l ul li a:hover span:nth-child(6),
  .nav-menu-l ul li a:hover span:nth-child(7) {
    animation: none;
  }
}
@media screen and (max-width: 768px) {
  .theme-app {
    top: 30px;
  }

  h2 {
    text-align: center;
  }
  .navigation {
    padding: 20px 30px;
  }
  .nav-menu {
    flex-direction: column;
  }
  .nav-menu-c {
    height: 25%;
  }
  .nav-menu-l{
    padding-top: 20%;
  }
  .nav-menu-l ul li a {
    font-size: calc(30px + 4vw);
    line-height: calc(47px + 3.5vw);
    text-align: center;
  }
  .nav-menu-l ul {
    transform: translateX(0);
    transition: none;
  }
  .act .nav-menu-l ul {
    transform: translateX(0);
    transition: none;
  }
  .nav-menu {
    transform: translateX(100%);
    transition: 0.4s cubic-bezier(0.7, 0, 0.3, 1);
  }
  .nav-menu.act {
    transform: translateX(0%);
    transition: 0.7s cubic-bezier(0.7, 0, 0.3, 1);
  }
  .nav-menu-l ul li:nth-child(1),
  .nav-menu-l ul li:nth-child(2),
  .nav-menu-l ul li:nth-child(3),
  .nav-menu-l ul li:nth-child(4) {
    transform: translateX(40vw);
    opacity: 0;
    transition: 0.5s cubic-bezier(0, 0, 0.2, 1) 0.5s;
  }
  .act .nav-menu-l ul li:nth-child(1),
  .act .nav-menu-l ul li:nth-child(2),
  .act .nav-menu-l ul li:nth-child(3),
  .act .nav-menu-l ul li:nth-child(4) {
    opacity: 1;
    transform: translateX(0);
    transition: 0.5s cubic-bezier(0, 0, 0.2, 1) 0.2s;
  }
  .act .nav-menu-l ul li:nth-child(1) {
    transition: 0.5s cubic-bezier(0, 0, 0.2, 1) 0.5s;
  }
  .act .nav-menu-l ul li:nth-child(2) {
    transition: 0.5s cubic-bezier(0, 0, 0.2, 1) 0.4s;
  }
  .act .nav-menu-l ul li:nth-child(3) {
    transition: 0.5s cubic-bezier(0, 0, 0.2, 1) 0.3s;
  }
  .act .nav-menu-l ul li:nth-child(4) {
    transition: 0.5s cubic-bezier(0, 0, 0.2, 1) 0.2s;
  }
  .nav-menu-c-a {
    text-align: center;
  }
  .nav-menu-c-b,
  .nav-menu-c-a div div:nth-child(1),
  .nav-menu-c-a div div:nth-child(2),
  .nav-menu-c-a div div:nth-child(3) {
    display: none;
  }
  .nav-menu-c-a div div:nth-child(4) div {
    display: flex;
  }
  .soc-icons {
    margin: 0 7px;
  }
  .nav-menu-c .nav-menu-c-a {
    transform: translateX(50%);
    transition: 0.1s linear 0.2s;
  }
  .act .nav-menu-c .nav-menu-c-a {
    transform: translateX(0);
    transition: 1s cubic-bezier(0.7, 0, 0.3, 1);
  }
  .nav-menu-c {
    justify-content: center;
    align-items: flex-start;
  }
  .nav-menu-c::before {
    width: 40%;
    height: 2px;
    left: auto;
    display: none;
  }
}
