.blog-navigation,
.card-content-container a,
.card-image-container,
.card-content {
  -webkit-touch-callout: none;
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  user-select: none;
}

.card-content-container {
  position: relative;
}

.link-img-cont {
  position: absolute;
  width: 70%;
  height: 100%;
  display: block;
  top: 0;
  left: 15%;
  z-index: 2;
  user-select: none;
}

.blog-navigation {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding: 0 0 calc(20px + 0.8vw) 0;
}

.blog-navigation h4 {
  color: #f9f9f9;
  cursor: pointer;
  transition: 0.4s ease;
}

.blog-navigation h4:hover {
  color: #bd9165;
}

/* .blog-navigation h4.active, */
/* .blog-navigation h4:active, */
.blog-navigation h4:focus {
  color: #bd9165;
  transform: scale(1.1);
}

.card-anim {
  opacity: 0;
  animation: 0.3s identifier ease-out forwards;
  animation-delay: 0.5s;
}

@keyframes identifier {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.card-image-container {
  position: relative;
  width: 80%;
  height: 30vw;
  min-height: 300px;
  background: #bd9165;
  overflow: hidden;
}
.card .card-image-container {
  left: auto;
}
.card:nth-child(2n + 1) .card-image-container {
  left: 10%;
}
.card-image-container .card-image {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #bd9165;
  transform: scale(1.3) translateX(0);
  transition: 0.9s cubic-bezier(0.7, 0, 0.3, 1);
}

.card-image-container a {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.card-image-container:hover .card-image {
  transform: scale(1) translateX(-20%);
}

.card-image-container .title-text {
  position: absolute;
  width: 50%;
  height: 100%;
  padding: 0 7%;
  z-index: 3;
  background: #bd9165;
  right: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transform: translateX(110%);
  visibility: hidden;
  transition: 0.9s cubic-bezier(0.7, 0, 0.3, 1);
  color: #f9f9f9;
}

.card-image-container:hover .title-text {
  visibility: visible;
  transform: translateX(0%);
}

.card-image-container .title-text div {
  transform: translateX(5vw);
  opacity: 0;
  transition: 0.6s cubic-bezier(0.7, 0, 0.3, 1);
}

.card-image-container:hover .title-text div {
  transform: translateX(0%);
  opacity: 1;
  transition: 1.2s cubic-bezier(0.7, 0, 0.3, 1);
}

.title-container{
  color: #f9f9f9;
}

.title-category {
  background: #f9f9f9;
  color: #222;
  position: absolute;
  z-index: 2;
  padding: calc(15px + 0.2vw) calc(20px + 0.4vw);
  display: flex;
  justify-content: center;
  align-items: center;
  top: 5%;
  left: 3%;
  user-select: none;
  visibility: hidden;
  opacity: 0;
  transform: translateX(4vw);
  transition: 0.9s cubic-bezier(0.7, 0, 0.3, 1);
}

.card-image-container:hover .title-category {
  visibility: visible;
  opacity: 1;
  transform: translateX(0);
}

.load-more-btn {
  margin: calc(40px + 1vw) 0 calc(20px + 0.8vw) 0;
}

.dekstop-load-more {
  color: #f9f9f9;
  cursor: pointer;
  user-select: none;
  transition: 0.4s ease;
  opacity: 1;
}

.dekstop-load-more:hover {
  opacity: 0;
}

.category {
  background: #f9f9f9;
  padding: 10px 15px;
  color: #222;
}

.content-pagination {
  position: relative;
  width: 100%;
  margin: calc(20px + 4vw) 0 0 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.content-pagination a {
  display: inline-block;
  text-decoration: none;
  color: #f9f9f9;
  transition: 0.4s ease;
  user-select: none;
}
.content-pagination a:hover {
  color: #bd9165;
  opacity: 0;
}
/* .img-cont-anim{
  height: 100vh;
} */

@media screen and (max-width: 1080px) {
  .card-image-container:hover img {
    transform: scale(1) translateX(0%);
  }
}

@media screen and (max-width: 768px) {
  .card-image-container {
    height: 30vh;
    width: 90%;
  }

  .load-more-btn {
    font-size: 16px;
    line-height: 16px;
  }
  .load-more-btn:hover::before {
    background: #f9f9f9;
    transform: scale(1);
  }
  .content-pagination a:hover {
    color: #bd9165;
    opacity: 1;
  }
}
