.panel {
  height: 100vh;
}

.panel h1 {
  font-size: 1.8em;
  color: white;
  font-weight: 300;
  margin: 0 auto;
}

.panel p,
.panel li {
  color: #f9f9f9;
  text-align: left;
  margin: 0.3em 0 1em 0;
}

.panel p code,
.panel li code {
  background-color: rgba(255, 255, 255, 0.15);
  padding: 2px 4px;
  border-radius: 5px;
}

.panel.description {
  padding-bottom: 60px;
}

.panel {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  position: relative;
  overflow: hidden;
  top: 0;
  left: 0;
}

.blue {
  flex-direction: row;
  user-select: none;
  pointer-events: none;
}

.red {
  height: 70vh;
  min-height: 700px;
  background: #bd9165;
}

.orange img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
}

.purple {
  background-color: #c4bb6b;
}

/* .gray {
  background-color: #777;
} */

.line {
  width: 100%;
  max-width: 1400px;
  height: 20px;
  margin-top: 20px;
  position: relative;
  z-index: 7;
  display: inline-block;
  background-color: rgba(255, 255, 255, 1);
}

.wel-h2 {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1;
  overflow: hidden;
}

.wel-h2 h2 {
  position: relative;
  /* font-size: calc(40px + 5.5vw);
  line-height: calc(30px + 5vw); */
  font-size: 8vw;
  line-height: 6.5vw;
  color:#f9f9f9;
  font-family: "Bebas Neue", cursive;
  cursor: default;
  text-align: right;
}

.wel-h2 h2.bp-h2-w {
  color: #bd9165;
}

.wel-h2 h4 {
  padding: 0;
}

.wel-h2-d {
  width: 100%;
  height: 100vh;
  position: relative;
  top: 0;
  right: 0;
  overflow: hidden;
}

.wel-h2-d::before {
  content: "";
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
  background: #141414;
  z-index: 7;
  transform: translateY(100%);
  transition: none;
}

.pre-anim.before-act .wel-h2-d::before {
  transform: translateY(0%);
}

.pre-anim.act .wel-h2-d::before {
  transform: translateY(100%);
  transition: 0.9s cubic-bezier(0.7, 0, 0.3, 1) 0.5s;
  visibility: hidden;
}

.bp {
  position: absolute;
  width: 100%;
  height: 101%;
  top: 0;
  left: 0;
  user-select: none;
  pointer-events: none;
  transform: translate3d(0, 0, 0);
}

.bp-0 {
  z-index: 7;
  height: 50vh;
  position: absolute;
  top: auto;
  bottom: -1vh;
  left: 0;
  background: linear-gradient(
    0deg,
    rgba(13, 13, 6, 1) 10%,
    rgba(13, 13, 6, 0) 100%
  );
  opacity: 0;
}

.bp-1 {
  z-index: 6;
  top: 5vh;
  background: url("./testpageImg/1.png") no-repeat 20% 0 / cover;
}
.bp-2 {
  z-index: 5;
  background: url("./testpageImg/2.png") no-repeat 0 / cover;
}
.bp-3 {
  z-index: 4;
  background: url("./testpageImg/3.png") no-repeat 0 / cover;
}
.bp-4 {
  z-index: 3;
  background: url("./testpageImg/4.jpg") no-repeat 0 / cover;
}

.scroll-down {
  position: absolute;
  bottom: 3vh;
  left: 50%;
  transform: translate(-50%, 0);
  color: #f9f9f9;
  font-weight: 400;
  text-transform: uppercase;
  font-size: 14px;
  overflow: visible;
  z-index: 7;
  opacity: 1;
  transition: none;
}

.pre-anim.before-act .scroll-down {
  opacity: 0;
}

.pre-anim.act .scroll-down {
  opacity: 1;
  transition: 1s ease-out 0.2s;
}

.bp-h2-w,
.bp-h4-w {
  opacity: 1;
  transform: translateX(0%);
  transition: none;
}

.pre-anim.before-act .bp-h2-w,
.pre-anim.before-act .bp-h4-w {
  opacity: 0;
  transform: translateX(50%);
}

.pre-anim.act .bp-h2-w,
.pre-anim.act .bp-h4-w {
  opacity: 1;
  transform: translateX(0%);
}

.pre-anim.act .bp-h2-w {
  transition: 0.9s cubic-bezier(0, 0, 0.2, 1) 0s;
}

.pre-anim.act .bp-h4-w {
  transition: 0.9s cubic-bezier(0, 0, 0.2, 1) 0.1s;
}

.page-nav {
  position: absolute;
  right: 5%;
  width: auto;
  height: 100vh;
  padding: 2vh 0;
  z-index: 20;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.page-nav-a {
  display: flex;
  flex-direction: column;
}

.page-nav-a a {
  width: 1.1vw;
  height: 1.1vw;
  min-width: 30px;
  min-height: 30px;
  display: flex;
  margin: 3px 0;
  font-size: 0%;
  position: relative;
  overflow: hidden;
}

.page-nav-a a::after {
  content: "";
  position: absolute;
  top: 0;
  width: 50%;
  height: 50%;
  min-height: 5px;
  transition: 0.5s ease-out;
  border-radius: 50%;
  transform: scale(0.4);
  background: #f9f9f9;
}

.page-nav-a a.active,
.page-nav-a a.active::after {
  transform: scale(1);
}

#sectionPin {
  height: 100vh;
  overflow: hidden;
  display: flex;
  left: 0;
  position: relative;
}

.pin-wrap {
  height: 100vh;
  /* min-width: 280vw; */
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0 2vw;
  user-select: none;
}

.pin-wrap h2 {
  /* min-width: 60vw; */
  padding: 0 1vw;
  color: #bd9165;
  transition: 0.5s ease;
}
.pin-wrap a {
  text-decoration: none;
}

.pin-wrap a:hover h2 {
  opacity: 0;
  transform: scale(0.95);
}

.pin-wrap .img-hor {
  width: 38vw;
  min-width: 600px;
  height: 65vh;
  position: relative;
  overflow: hidden;
  margin: 0 3vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pin-wrap .img-hor .img-cont {
  transform: scale(1.16);
  transition: 0.8s ease-in-out;
}

.pin-wrap .img-hor a:hover .img-cont {
  transform: scale(1.01);
}

.skewElemPrlx {
  position: absolute;
  width: 70%;
  right: 1vw;
  bottom: -2vw;
  z-index: 2;
  color: #f9f9f9;
  pointer-events: none;
}

.img-cont-anim{
  width: 100%;
  height: 100vh;
  position: absolute;
  overflow: hidden;
  margin-top: -50vh;
  z-index: 5;
}

@media screen and (max-width: 1024px) {
  .wel-h2 h2 {
    font-size: 8vw;
    line-height: 7vw;
  }
  .blue {
    flex-direction: column;
  }
  .page-nav {
    display: none;
  }
  #sectionPin {
    height: auto;
    display: flex;
    left: 0;
    position: relative;
  }
  .pin-wrap {
    height: auto;
    width: 100%;
    min-width: 100px;
    flex-direction: column;
    align-items: unset;
    position: relative;
    padding: 0;
  }
  .pin-wrap h2{
    margin: 60px 10%;
  }
  .pin-wrap .img-hor {
    width: 100%;
    min-width: 100px;
    height: 50vh;
    position: relative;
    overflow: hidden;
    margin: 0;
    padding: 0;
    left: 0;
  }
  .skewElemPrlx {
    position: relative;
    width: 100%;
    right: auto;
    bottom: auto;
    color: #f9f9f9;
    pointer-events: none;
    text-align: center;
    margin: 20px 0 30px 0;
    padding: 0 10%;
  }
}

@media screen and (max-width: 768px) {
  .wel-h2 h2 {
    font-size: calc(40px + 5.5vw);
    line-height: calc(35px + 4.5vw);
    padding-top: 20px;
  }
  .blue {
    flex-direction: column;
  }
}
